//获取URL参数
export function getUrlKey(name) {
    return decodeURIComponent((new RegExp('[?|&]'+name+'='+
      '([^&;]+?)(&|#|;|$)').exec(location.href)||[,""])[1].replace(/\+/g,'%20'))||'';
}


