<template>
  <div>
    <header class="index-head">
      <div class="page-width">
        <img src="../assets/img/logo.png" height="39" width="100" alt="">
      </div>
    </header> 
    

    <div class="task-con page-width">
      <div class="task-tit clearfix">
        <h3 class="left">基本信息</h3>
        <div class="right">
          <i class="el-icon-question text-gray"></i>
          <span class="color-blue help" @click="dialogVisible1 = true">帮助</span>
        </div>
      </div>
        
      <div class="task-list">
        <div class="clearfix">
          
          <div class="demo-input-suffix left">
            <span class="size14">任务名称</span>
            <el-input
              placeholder="建议命名：爬取网站名称+内容细节"
              v-model="name">
            </el-input>
          </div>
          <div class="demo-input-suffix right">
            <span class="size14">作者</span>
            <el-input
              placeholder="配置该任务的责任人"
              v-model="author">
            </el-input>
          </div>
        </div>
        <div class="clearfix">
          
          <div class="demo-input-suffix left">
            <span class="size14">起始URL</span>
            <el-input
              placeholder="要爬取的网站第一个列表URL"
              v-model="beginUrl">
            </el-input>
          </div>
          <div class="demo-input-suffix right">
            <span class="size14">任务调度间隔</span>
            <el-input
              placeholder="单位：分钟， 最小60分钟，建议配置 60 或 1440（天）"
              v-model="spacing">
            </el-input>
          </div>
        </div>
        <div class="block">
          <span class="size14">遍历网站</span>
          <div class="">
            <el-select v-model="blText" @change = "showText">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <el-popover
            placement="top-start"
            title=""
            width="50"
            trigger="hover"
            content="这是一段内容,这是一段内容,这是一段内容,这是一段内容。这是一段内容这是一段内容这是一段内容">
            <i slot="reference" class="el-icon-question icon-wh text-gray"></i>
            <!-- <el-button slot="reference">hover 激活</el-button> -->
          </el-popover>
          
        </div>

       <div class="clearfix">
         
        <div class="demo-input-suffix left">
          <span class="size14">Header</span>
            <el-input
              placeholder="不支持正则表达式，只需要支持把chrome浏览器插件里面获得的 key value每行一条，保存即可.例如：
    Origin: https://www.showdoc.cc
    Sec-Fetch-Site: same-origin
    User-Agent: Mozilla/5.0 (Windows NT 6.1; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/81.0.4044.138 Safari/537.36"
          type="textarea"
    :autosize="{ minRows: 4, maxRows: 8}"
              v-model="headerCon">
            </el-input>
          </div>
          <div class="demo-input-suffix right">
            <span class="size14">数据库定制保存字段</span>
            <el-input
              placeholder="给后台清洗时使用的，属于配合操作，
  {
  'province' : '浙江省',
  'dns_name' : 'wzzbtb'
  } 
  province字段可以填写省市信息，如果该网站是固定某个市的数据，这样能减少配置获取市信息的规则；
  dns_name字段，填写网站域名中的独特字段，用于后台区分网站数据（例如：www.wzzbtb.com）"
              type="textarea"
    :autosize="{ minRows: 4, maxRows: 8}"
              v-model="fieldCon">
            </el-input>
          </div>
       </div>

      </div>
    </div>

    

    <div class="task-con page-width">
      <div class="task-tit clearfix">
        <h3 class="left">列表页信息</h3>
        <div class="right">
          <span class="color-blue help" @click="showList">验证列表页参数</span>
        </div>
      </div>
        
      <div class="task-list">
        <div class="clearfix">
          
          <div class="demo-input-suffix left">
            <span class="size14">请求方式</span>
            <div>
              <el-radio v-model="radio" label="GET" @change="getValue3()">GET</el-radio>
              <el-radio v-model="radio" label="POST" @change="getValue3()">POST</el-radio>
            </div>
            <!-- <el-input
              placeholder="建议命名：爬取网站名称+内容细节"
              v-model="input2">
            </el-input> -->
          </div>
          <div class="demo-input-suffix right">
            <span class="size14">返回数据格式</span>
            <div>
              <el-radio v-model="radio2" label="HTML" @change="getValue2()">HTML</el-radio>
              <el-radio v-model="radio2" label="JSON" @change="getValue2()">JSON</el-radio>
            </div>
          </div>
        </div>
        <div class="block1">
          <div class="">
            <span class="size14">请求参数</span>
            <el-input
              placeholder="POST类型会有参数，直接从chrome里面复制，翻页参数如何控制在其他地方描述"
              v-model="search">
            </el-input>
          </div>
        </div>

       <div class="block1">
         
        <div class="">
            <span class="size14">获取数据规则配置</span>
            <el-input
              placeholder="列表的数据会返回多个链接，数据有规则，用xpath提取，可提取 详情infor_url/标题title/下一页next_page，新闻日期date尽量提取，省得在正文里面提取 ，举例{ 
'infor_url' : '//xpath1',
'title':'//xpath2',
'date':'//xpath3',
'next_page' : '//xpath4'
}"
          type="textarea"
    :autosize="{ minRows: 4, maxRows: 8}"
              v-model="rule">
            </el-input>
          </div>
       </div>

      </div>
    </div>


    <div class="task-con page-width">
      <div class="task-tit clearfix">
        <h3 class="left">详情页信息</h3>
        <div class="right">
          <!-- <i class="el-icon-question color-red"></i> -->
          <span class="color-blue help" @click="showDeatil">验证详情页参数</span>
        </div>
      </div>
        
      <div class="task-list">
        <div class="clearfix">
          
          <div class="demo-input-suffix left">
            <span class="size14">请求方式</span>
            <div>
              <el-radio v-model="radio3" label="GET" @change="getValue1()">GET</el-radio>
              <el-radio v-model="radio3" label="POST" @change="getValue1()">POST</el-radio>
            </div>
            <!-- <el-input
              placeholder="建议命名：爬取网站名称+内容细节"
              v-model="input2">
            </el-input> -->
          </div>
          <div class="demo-input-suffix right">
            <span class="size14">返回数据格式</span>
            <div>
              <el-radio v-model="radio4" label="HTML" @change="getValue()">HTML</el-radio>
              <el-radio v-model="radio4" label="JSON" @change="getValue()">JSON</el-radio>  
            </div>
          </div>
        </div>
      <div class="block1">
        <div class="">
          <span class="size14">请求参数</span>
          <el-input
            placeholder="POST类型会有参数，直接从chrome里面复制，翻页参数如何控制在其他地方描述"
            v-model="search2">
          </el-input>
        </div>
      </div>

       <div class="clearfix">
         
        <div class="demo-input-suffix left">
            <span class="size14">获取正文规则配置</span>
            <el-input
              placeholder="用xpath提取正文，举例
 {
'html_code': '//div[@class='Main-p']'
}"
          type="textarea"
    :autosize="{ minRows: 4, maxRows: 8}"
              v-model="rule2">
            </el-input>
        </div>

        <div class="demo-input-suffix right">
          <span class="size14">* 新闻是否清洗</span>
          <div class="">
            <el-select v-model="newText" value-key="id" @change="getIncrement">
              <el-option
                v-for="item in options1"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          
        </div>
      </div>

    </div>
  </div>


  <div class="page-width save-btn" @click="saveInfo">
    <span class="right">保存任务</span>
  </div>
  
  <div>
    <el-dialog title="" :visible.sync="dialogTableVisible" :before-close="handleClose">
      <p class="next-page"><span>next_page:</span> {{next_page}}</p>
      <el-table :data="gridData">
        <el-table-column property="title" label="title" width="350"></el-table-column>
        <el-table-column property="infor_url" label="infor_url" width="350"></el-table-column>
        <el-table-column property="date" label="date" width="200"></el-table-column>
      </el-table>
    </el-dialog>
  </div>
  <!-- :before-close="handleClose" -->
  <div>
    <el-dialog
      :visible.sync="dialogVisible1"
      width="55%"
      >
      <div class="pop-text">
        <div>
          <h4>常用定位表达式</h4>
          <p>表达式描述示例nodename     选取此节点的所有子节点     div、ul、li、a、h1等等/     假如路径起始于正斜杠(/)，则此路径始终道标到某元素的绝对路径！     /body//     选取所有子元素，而不管它们在文档中的位置     //a.     选取当前节点    ..     选取当前节点的父节点     /body*     匹配任何元素节点    |     多个表达式同是匹配     //h1/a|//ul/li/a@*     匹配任何属性节点    更多XPath 语法请参考http://www.w3school.com.cn/xpath/xpath_syntax.asp</p>
        </div>

        <div>
          <h4>常用解析表达式</h4>
          <p>1、text 文本信息，不包含html标签2、html 源代码，包含html标签</p>
        </div>

        <div>
          <h4>高级函数</h4>
          <p>1、split(分隔符,数组下标): 将字符串分割成数组，取对应下标的字符串。例："百度-1小时前".split(-,0) = "百度"2、remove(字符串1,字符串2,...): 移除指定字符串，参数个数不限。例："作者：xxx".remove(作者：) = "xxx"  * 参数中的特殊符号: 点“.”需要用“\.”代替，英文逗号“,”需要用“\,”代替。*</p>
        </div>

        <div>
          <h4>分页表达式</h4>
          <p>示例：http://news.baidu.com/ns?word=java&pn=-100   描述：起始页-末页|步长},{起始页-末页},{末页|步长  * 起始页和步长默认等于1，如果只取一页，可以不使用表达式方式*</p>
        </div>

        <div>
          <h4>搜索功能</h4>
          <p>URL和模板名称支持模糊搜索</p> 
        </div>
      </div>
    </el-dialog>
  </div>
    

</div>

  
</template>

<script>

import axios from 'axios'
import {getUrlKey} from '../utils/api.js'

export default {
  data () {
    return {
      total:0,
      input2:'',
      value:true,
      increment:false,
      content_clean:true,
      newText:'清洗',
      blText:'不遍历',
      // value1: '清洗',
      radio: 'GET',
      radio3: 'GET',
      radio2:'HTML',
      radio4:'HTML',
      options: [{
          id:0,
          value: '选项一',
          label: '不遍历'
        },
         {
          id:1,
          value: '选项二',
          label: '遍历'
        }],
        value: '遍历',
        options1: [{
          id:0,
          value: '选项一',
          label: '清洗'
        }, {
          id:1,
          value: '选项二',
          label: '不清洗'
        }],  
        gridData: [],
        dialogTableVisible: false,
        dialogVisible1:false,
        name:'',
        beginUrl:'',
        author:'',
        spacing:'',
        headerCon:'',
        fieldCon:'',
        rule:'',
        rule2:'',
        search:'',
        search2:'',
        id:getUrlKey('id'),
        infoText:{},
        detailList:[],
        next_page:''
    }
  },
  methods:{
    
    getValue(){
      //alert(this.radio4)
      //alert(this.radio3)
      this.radio4 = this.radio4
    },
    getValue1(){
      //alert(this.radio4)
      //alert(this.radio3)
      this.radio3 = this.radio3
    },

    getValue2(){
      this.radio2 = this.radio2
          //alert(this.radio4)
          //alert(this.radio3)
    },

    getValue3(){
      this.radio = this.radio
          //alert(this.radio4)
          //alert(this.radio3)
    },
    getIncrement(val){
      //alert(this.newText)
      if( this.newText == '选项一'){
        this.content_clean = true
      }else{
        this.content_clean = false
      }
      
      //alert(this.increment)
    },
    showText(val){
     // alert(this.newText)
      //this.increment = this.increment
      if( this.blText == '选项一'){
        this.increment = false
      }else{
        this.increment = true
      }
    },
    saveInfo(){

      if(this.id == '') {
        var data ={
          "task_name":this.name,
          "author":this.author,
          "url":this.beginUrl,
          "job_interval":this.spacing,
          "headers":this.headerCon,
          "increment":this.increment,
          "default_values":this.fieldCon,
          "pipeline":[{
            "page_type" : this.radio2,
            "method" : this.radio,
            "params" :this.search,
            "data" :this.rule
          },
          {
            "page_type" : this.radio4,
            "method" : this.radio3,
            "params" : this.search2,
            "content_clean":this.content_clean,
            "data":  this.rule2
          }]
         }
         const rLoading = this.openLoading();
        this.$axios.post('/api/template', data)
        .then( res => {
          rLoading.close();
          if(res.data.status == "200"){
              this.$message.success(res.data.msg);
              this.$router.push({name:'index'})
          }else{
            this.$message.error(res.data.msg);
          }
        }).catch( error => {
          //this.$message.error(error);
        });

      }else{
        var data ={
          "task_name":this.name,
          "author":this.author,
          "url":this.beginUrl,
          "job_interval":this.spacing,
          "headers":this.headerCon,
          "increment":this.increment,
          "default_values":this.fieldCon,
          "pipeline":[{
            "page_type" : this.radio2,
            "method" : this.radio,
            "params" :this.search,
            "data" :this.rule
          },
          {
            "page_type" : this.radio4,
            "method" : this.radio3,
            "params" : this.search2,
            "content_clean":this.content_clean,
            "data":  this.rule2
          }]
         }
         const rLoading = this.openLoading();
        this.$axios.post('/api/template/'+ this.id, data)
        .then( res => {
          rLoading.close();
          if(res.data.status == "200"){
              this.$message.success(res.data.msg);
              this.$router.push({name:'index'})
          }else{
            this.$message.error(res.data.msg);
          }
        }).catch( error => {
          //this.$message.error(error);
        });
      }
       

    },

    showList(){
      //this.dialogTableVisible = true
      var data ={
        "url":this.beginUrl,
        "headers":this.headerCon,
        "pipeline":{
            "page_type" : this.radio2,
            "method" : this.radio,
            "params" :this.search,
            "data" :this.rule

        }
      }
      const rLoading = this.openLoading();
      this.$axios.post('/api/verify/nav_test', data)
      .then( res => {
        rLoading.close();
        this.dialogTableVisible = true
        if(res.data.status == "200"){
            //this.$message.success(res.data.msg);
            //this.dialogTableVisible = true
            this.next_page = res.data.next_page
            this.gridData = res.data.data
        }else{
          this.$message.error(res.data.msg);
        }
      }).catch( error => {
        //this.$message.error(error);
      });

    },

    handleClose(done) {
      //alert(111)
      this.gridData = []
      this.dialogTableVisible = false
    },
    showDeatil(){
      //this.dialogTableVisible = true
      var data ={
        "url":this.beginUrl,
        "headers":this.headerCon,
        "default_values":this.fieldCon,
        "pipeline":[{
            "page_type" : this.radio2,
            "method" : this.radio,
            "params" :this.search,
            "data" :this.rule
            //  {
            //     "infor_url" : "//div[@class='List-Li FloatL']/ul/li/a/@href",
            //     "title" : "//div[@class='List-Li FloatL']/ul/li/a/@title",
            //     "date" : "//div[@class='List-Li FloatL']/ul/li/span/text()",
            //     "next_page" : "//div[@class='Zy-Page FloatL']/div/a[3]/@href"
            // }
        },
        {
            "page_type" : this.radio4,
            "method" : this.radio3,
            "params" : this.search2,
            "content_clean":this.content_clean,
            "data":  this.rule2
            // {
            //     "html_code" : "//div[@class='Main-p']"
            // }
        }]
      }
      const rLoading = this.openLoading();
      this.$axios.post('/api/verify/detail_test', data)
      .then( res => {
        rLoading.close();
        if(res.data.status == "200"){
            this.detailList = JSON.stringify(res.data.data)
            localStorage.setItem("detailList",this.detailList);
            //this.$router.push({name:'detail'})
            const { href } = this.$router.resolve({
              name: "detail",
            });
            window.open(href, '_blank')
            
        }else{
          this.$message.error(res.data.msg);
        }
      }).catch( error => {
        //this.$message.error(error);
      });

    }
  },

  created(){

     
    //alert(this.id)
    //const rLoading = this.openLoading();
    this.$axios.get('/api/template/'+this.id
    //   , {
    //   // params:{
    //   //   // "":this.id
    //   //   // "filter":'',
    //   //   // "page":1,
    //   //   // "count":10,
    //   // },
    //   timeout: 10000 
    // }
    )
    
    .then( res => {
       //rLoading.close();
      if(res.data.status == "200"){
        this.infoText = res.data.data
        
        this.beginUrl = this.infoText.url

        if( this.infoText.headers == null){
          this.headerCon = ''
        }else{
          this.headerCon = JSON.stringify(this.infoText.headers)
        }

        this.radio2 = this.infoText.pipeline[0].page_type
        this.radio = this.infoText.pipeline[0].method
        this.increment = this.infoText.increment

        if( this.increment == false){
          this.blText = '选项一'
        }else{
          this.blText = '选项二'
        }
        this.radio4 = this.infoText.pipeline[1].page_type
        this.radio3 = this.infoText.pipeline[1].method
        this.content_clean = this.infoText.pipeline[1].content_clean

        if( this.content_clean == true){
          this.newText = '选项一'
        }else{
          this.newText = '选项二'
        }

        // if( this.infoText.pipeline[0].page_type == "GET") {
        //   this.radio2 = 
        // }
        //this.headerCon = JSON.stringify(this.infoText.headers)
        this.fieldCon = JSON.stringify(this.infoText.default_values)
        this.author = this.infoText.author

        this.search = JSON.stringify(this.infoText.pipeline[0].params)
        this.rule = JSON.stringify(this.infoText.pipeline[0].data)
        this.spacing = this.infoText.job_interval
        this.name = this.infoText.task_name
        this.search2 = JSON.stringify(this.infoText.pipeline[1].params)
        this.rule2 = JSON.stringify(this.infoText.pipeline[1].data)

        // "pipeline":[{
        //     "page_type" : this.radio2,
        //     "method" : this.radio,
        //     "params" :this.search,
        //     "data" :this.rule
        //     //  {
        //     //     "infor_url" : "//div[@class='List-Li FloatL']/ul/li/a/@href",
        //     //     "title" : "//div[@class='List-Li FloatL']/ul/li/a/@title",
        //     //     "date" : "//div[@class='List-Li FloatL']/ul/li/span/text()",
        //     //     "next_page" : "//div[@class='Zy-Page FloatL']/div/a[3]/@href"
        //     // }
        // },
        // {
        //     "page_type" : this.radio4,
        //     "method" : this.radio3,
        //     "params" : this.search2,
        //     "content_clean":this.content_clean,
        //     "data":  this.rule2
        //     // {
        //     //     "html_code" : "//div[@class='Main-p']"
        //     // }
        // }
      }
    }).catch( error => {
      this.$message.error(error);
    });
  }
}
</script>

<style>
  
</style>



