<template>
  <div>

    <header class="index-head">
      <div class="page-width">
        <img src="../assets/img/logo.png" height="39" width="100" alt="">
      </div>
    </header>
    
    <div class="page-width">
			<div class="search-input">
			  <el-input placeholder="请输入任务名称，作者，起始URL信息" v-model.trim="taskWord" class="input-with-select" @keyup.enter.native="searchTask">
			    <el-button slot="append" @click="searchTask">搜索</el-button>
			  </el-input>
			</div>
			<div class="customized color-blue" @click="goTask1">
        <span>新建任务</span>
      </div>
		</div>
		
		<div class="company-list page-width">
				<!-- <p class="company-list-text text-right">总共有{{sumtotal}}家企业</p> -->
				<el-row class="company-list-tit" type="flex" align="middle">
				  <el-col :span="5">
				  	<div class="grid-content bg-purple text-center" >任务名称</div>
				  </el-col>
				  <el-col :span="2">
				  	<div class="grid-content bg-purple text-center">作者</div>
				  </el-col>
				  <el-col :span="8">
				 		<div class="grid-content bg-purple text-center">起始URL</div>
				 	</el-col>
				  <el-col :span="2">
				  	<div class="grid-content bg-purple text-center">上次任务启动时间</div>
				  </el-col>
				  <el-col :span="2">
				  	<div class="grid-content bg-purple text-center">下次任务启动时间</div>
				  </el-col>
				  <el-col :span="2">
				  	<div class="grid-content bg-purple text-center">下次任务状态</div>
				  </el-col>
				  <el-col :span="2">
				  	<div class="grid-content bg-purple text-center">任务修改时间</div>
				  </el-col>
				  <el-col :span="1">
				  	<div class="grid-content bg-purple text-center">操作</div>
				  </el-col>
				</el-row>
				<div class="company-list-color" v-if="taskList != '' && taskList != null"> 
					<el-row type="flex" align="middle" class="company-list-content" v-for = "(task,index) in taskList" :key="index" @click.native="goTaskDetail(task._id,task.task_name)">
					  <el-col :span="5">
					  	<div class="grid-content bg-purple text-center">{{task.task_name}}</div>
					  </el-col>
					  <el-col :span="2">
					  	<div class="grid-content text-center">{{task.author}}</div>
					  </el-col>
					  <el-col :span="8">
					  	<div class="grid-content bg-purple text-center" style="word-wrap: break-word;">{{task.url}}</div>
					  </el-col>
					  <el-col :span="2">
					  	<div class="grid-content bg-purple text-center">{{task.last_fetch_time}}</div>
					  </el-col>
					  <el-col :span="2">
					  	<div class="grid-content bg-purple text-center">{{task.job_next_time}}</div>
					  </el-col>
					  <el-col :span="2">
					  	<div class="grid-content bg-purple text-center">
					  			<el-switch
					  				@change = "showType(task.is_run,task._id)"
									  v-model="task.is_run"
									  active-color="#409EFF"
									  inactive-color="#999">
									</el-switch>
					  	</div>
					  </el-col>
					  <el-col :span="2">
					  	<div class="grid-content bg-purple text-center">{{task.last_update_time}}</div>
					  </el-col>
					  <el-col :span="1">
					  	<div class="grid-content bg-purple text-center color-blue" style="text-decoration: underline;" @click="goTask(task._id)">编辑</div>
					  </el-col>
					</el-row>
					
				</div>
				<div class="company-list-color" v-else> 
					<el-row type="flex" align="middle" class="company-list-content">
					  <el-col :span="24">
					  	<div class="grid-content bg-purple text-center">暂无数据</div>
					  </el-col>
					  
					</el-row>
				</div>
				<div class="num-total right">
					<el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page.sync="currentPage1"
			      :page-size="pageSize"
			      layout="total, prev, pager, next"
			      :total="total_count">
			    </el-pagination>
				</div>
				<!-- <div class="num-total">
					<div class="pagination-num">
						<el-pagination
						  background
						  :pager-count="5"
						  :hide-on-single-page="value2"
						  layout="prev, pager, next"
						  :page-size='pageSize'
						  :total="total_count"
						  :current-page.sync="currentPage"
							 @current-change="changePage"
						  >
						</el-pagination>
					</div>
					<p>任务总数：{{this.total_count}}个</p> 
					
				</div> -->
				
			</div>
			
  </div>
</template>

<script>
export default {
  data () {
    return {
    	total_count:0,
    	sumtotal:'',
      taskWord:'',
      pageSize:10,
      value:true,
      currentPage:1,
      value2: true,
      taskList:[]
    }
  },
  methods:{
  	goTask(id){
  		const { href } = this.$router.resolve({
        name: "task",
        query:{
        	id:id
        }
      });
      window.open(href, '_blank');
  	},
  	goTask1(){
  		const { href } = this.$router.resolve({
        name: "task",
      });
      window.open(href, '_blank');
  	},
  	handleCurrentChange(val){
  			this.$axios.get('/api/template', {
		      params:{
		        "filter":this.taskWord,
		        "page":val,
		        "count":10,
		      },
		      timeout: 10000 
		    })
		    .then( res => {
		      //rLoading.close();
		      //console.log(res.data)
		      this.total_count = res.data.total_count
		      //alert(res.data.total_count)
		      if(res.data.status == "200"){
		          this.taskList = res.data.data
		          
		      }
		    }).catch( error => {
		      this.$message.error(error);
		    });
  	},
  	// goTaskDetail(id,taskname){
  	// 	const { hret } = this.$router.resolve({
  	// 			name:"task",
  	// 			query:{
  	// 				id:id
  	// 			}
  	// 	})
  	// 	window.open(href, '_blank');
  	// },
  	// changePage:function(){

  	// },
  	showType(type,id){
  			//alert(type)
  		var data ={
  				"_id":id,
  				"state":type
  		}
 			this.$axios.post('/api/template/switch_state',data)
 			.then( res => {
 				 if(res.data.status == '200'){
 				 		this.$message.success(res.data.msg);
 				 }else{
 				 		this.$message.error(res.data.msg);
 				 }
 			})

  	},

  	searchTask(){
  		this.$axios.get('/api/template', {
	      params:{
	        "filter":this.taskWord,
	        "page":1,
	        "count":10,
	      },
	      timeout: 10000 
	    })
	    .then( res => {
	      //rLoading.close();
	      //console.log(res.data)
	      this.total_count = res.data.total_count
	      //alert(res.data.total_count)
	      if(res.data.status == "200"){
	          this.taskList = res.data.data
	          
	      }
	    }).catch( error => {
	      this.$message.error(error);
	    });
  	}

  },

  created(){
    this.$axios.get('/api/template', {
      params:{
        "filter":'',
        "page":1,
        "count":10,
      },
      timeout: 10000 
    })
    .then( res => {
      //rLoading.close();
      //console.log(res.data)
      this.total_count = res.data.total_count
      //alert(res.data.total_count)
      if(res.data.status == "200"){
          this.taskList = res.data.data
          
      }
    }).catch( error => {
      this.$message.error(error);
    });
  }
}
</script>

<style>
  
</style>



