<template>
  <div>

    <header class="index-head">
      <div class="page-width">
        <img src="../assets/img/logo.png" height="39" width="100" alt="">
      </div>
    </header>
    
    <div class="page-width detail-con" style="padding-bottom:120px;">
      
      <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
        <el-tab-pane v-for="(item,index) in detailList" 
          :name="index"
          :key="index"
          :label="'新闻'+ (index + 1) ">
          <div class="news-con">
            <div class="detail-list clearfix">
              <span class="span-left">详情页URL</span>
              <p>{{item.infor_url}}</p>
            </div>

            <div class="detail-list clearfix">
              <span class="span-left">标题</span>
              <p>{{item.title}}</p>
            </div>

            <div class="detail-list clearfix">
              <span class="span-left">dns_name</span>
              <p>{{item.dns_name}}</p>
            </div>

            <div class="detail-list clearfix">
              <span class="span-left">date</span>
              <p>{{item.date}}</p>
            </div>

            <div class="detail-list clearfix">
              <span class="span-left">province</span>
              <p>{{item.province}}</p>
            </div>
            
            <div class="detail-list clearfix">
              <span class="span-left">html_code</span>
              <p v-html="item.html_code" class="html-text">{{item.html_code}}</p>
            </div>

          </div>
        </el-tab-pane>
        <!-- <el-tab-pane label="新闻2" name="second">新闻2</el-tab-pane>
        <el-tab-pane label="新闻3" name="third">新闻3</el-tab-pane>
        <el-tab-pane label="新闻4" name="fourth">新闻4</el-tab-pane> -->
      </el-tabs>
    </div>
      
  </div>
</template>

<script>
export default {
  data () {
    return {
      total:0,
      sumtotal:'',
      companyWord:'',
      pageSize:10,
      value:true,
      currentPage:1,
      activeName: 0,
      detailList:[],
      index:0
    }
  },
  methods:{
    handleClick(tab, event) {
      console.log(tab, event);
    }
  },
  created(){
    this.detailList = JSON.parse(localStorage.getItem('detailList'))
    console.log(this.detailList)
  }
}
</script>

<style>
  
</style>




